import React from 'react'
import './Sidebar.css'
import SidebarRow from './SidebarRow'
import HomeIcon from '@mui/icons-material/Home';
import DownloadIcon from '@mui/icons-material/Download';
import SubscriptionsIcon from '@mui/icons-material/Subscriptions';
import { ExpandMoreOutlined, History, OndemandVideo, ThumbUpAltOutlined, VideoLibrary, WatchLater } from '@mui/icons-material';
function Sidebar() {
  return (
    <div className='sidebar'>
    <SidebarRow selected  Icon = {HomeIcon} title = "Home"/>
    <SidebarRow  Icon = {DownloadIcon} title = "Trending"/>
    <SidebarRow Icon = {SubscriptionsIcon} title = "Subscription"/>
    {/* <h2> <HomeIcon/> Home</h2>
    <h2><WhatshotIcon/>Trending</h2>
    <h2><SubscriptionIcon/> Subscription</h2> */}
    <hr/>
    <SidebarRow Icon ={VideoLibrary} title = "Library" />
    <SidebarRow Icon ={History} title = "history" />
    <SidebarRow Icon ={OndemandVideo} title = "Your Videos" />
    <SidebarRow Icon ={WatchLater} title = "Watch Later" />
    <SidebarRow Icon ={ThumbUpAltOutlined} title = "Liked Video" />
    <SidebarRow Icon ={ExpandMoreOutlined} title = "show More" />

    </div>
  );
}

export default Sidebar